import { actionTypes } from ".";
import { fromJS } from "immutable";

const initialSearchTrendListAction = (list, totalPage) => ({
  type: actionTypes.INITIAL_SEARCH_TREND_LIST,
  list: fromJS(list),
  totalPage: fromJS(totalPage),
});

export const changeSearchFocusAction = (value) => ({
  type: actionTypes.CHANGE_SEARCH_FOCUS_STATE,
  value: fromJS(value),
});

export const getSearchTrendList = () => {
  return (dispatch) => {
    fetch("https://ypx0352.github.io/docs/data/searchTrend.json")
      .then((response) => response.json())
      .then((data) => {
        dispatch(
          initialSearchTrendListAction(
            data.data,
            Math.ceil(data.data.length / 12)
          )
        );
      })
      .catch((e) => {
        console.log(e);
      });
  };
};

export const changeSearchTrendPageAction = () => ({
  type: actionTypes.CHANGE_PAGE,
});

export const changeMouseEnterAction = (value) => ({
  type: actionTypes.CHANGE_MOUSE_ENTER,
  value: fromJS(value),
});

export const changeChangeFocusAction = () => ({
  type: actionTypes.CHANGE_CHANGE_FOCUS,
});
