import styled from "styled-components";
import logoImage from "../../statics/jianshu_logo.png";

export const HeaderWrapper = styled.div`
  z-index: 1;
  position: relative;
  height: 56px;
  border-bottom: 1px solid #f0f0f0;
`;

export const Logo = styled.div`
  position: absolute;
  display: block;
  left: 0px;
  top: 0px;
  height: 56px;
  width: 100px;
  background: url(${logoImage});
  background-size: contain;
`;

export const Nav = styled.div`
  width: 70%;
  margin: 0 auto;
  height: 100%;
  padding-right: 100px;
  box-sizing: border-box;
`;

export const NavItem = styled.div`
  &.left {
    float: left;
  }
  &.right {
    float: right;
    color: #969696;
  }
  &.active {
    color: #ea6f5a;
  }
  line-height: 56px;
  width: 56px;
  font-size: 17px;
  padding: 0 15px;
  color: #333;
`;

export const Searchwrapper = styled.div`
  position: relative;
  float: left;
  height: 38px;
  margin-top: 9px;
  .iconfont {
    position: absolute;
    right: 9px;
    top: 9px;
    width: 20px;
    line-height: 20px;
    text-align: center;
    border-radius: 10px;
    transition: 0.5s;
    &.focused {
      color: #fff;
      background: #777;
      transition: 0.5s;
    }
  }
`;

export const NavSearch = styled.input.attrs({ placeholder: "search" })`
  width: 160px;
  height: 38px;
  border: none;
  outline: none;
  border-radius: 19px;
  background: #eee;
  color: #666;
  padding: 0 35px 0 20px;
  box-sizing: border-box;
  font-size: 14px;
  margin-left: 20px;
  &::placeholder {
    color: #999;
  }
  transition: 0.5s;
  &.focused {
    width: 200px;
    transition: 0.5s;
  }
`;

export const SearchInfo = styled.div`
  position: absolute;
  left: 20px;
  top: 40px;
  width: 240px;
  padding: 0 20px;
  overflow: auto;
  background: white;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
  &.hide {
    display: none;
  }
`;

export const SearchInfoTitle = styled.div`
  color: #969696;
  margin-top: 20px;
  margin-bottom: 15px;
  line-height: 20px;
  font-size: 14px;
  width: 240px;
`;
export const SearchInfoTitleChange = styled.span`
  position: relative;
  float: right;
  width: 100px;
  font-size: 13px;
  color: #969696;
  background: red;
  cursor: pointer;
  .iconfont {
    display: block;
    position: absolute;
    left: 0px;
    top: 0px;
    font-size: 15px;
    &.clicked {
      transform: rotate(180deg);
      transform-origin: center;
      transition: transform 0.2s;
    }
  }
  .text {
    position: absolute;
    left: 20px;
  }
`;

export const SearchInfoItem = styled.a`
  display: block;
  float: left;
  line-height: 20px;
  padding: 0 5px;
  font-size: 12px;
  margin-right: 10px;
  margin-bottom: 10px;
  border: 1px solid #ddd;
  color: #787878;
  border-radius: 3px;
`;

export const Addition = styled.div`
  position: absolute;
  right: 0;
  top: 0;
  height: 56px;
`;

export const AdditionItem = styled.div`
  position: relative;
  float: right;
  width: 56px;
  line-height: 30px;
  border-radius: 20px;
  border: 1px solid #ec6149;
  margin-right: 19px;
  padding: 0 20px;
  margin-top: 12px;
  font-size: 14px;
  text-align: center;
  vertical-align: middle;
  &.register {
    color: #ec6149;
  }
  &.compose {
    color: white;
    background: #ec6149;
    text-align: center;
  }
  .iconfont {
    position: absolute;
    left: 12px;
  }
  .text {
    position: relative;
    left: 10px;
  }
`;
