import { fromJS } from "immutable";
import { actionTypes } from ".";

export const initializeAccountListAction = (dispatch) => {
  fetch("https://ypx0352.github.io/docs/data/login.json")
    .then((response) => response.json())
    .then((data) => {
      dispatch({
        type: actionTypes.INITIAL_ACCOUNT_LIST,
        value: fromJS(data.accountList),
      });
    });
};

export const loginAction = (userName, password, accountList) => {
  const list = accountList.toJS();
  for (let index = 0; index < list.length; index++) {
    if (
      list[index].userName === userName &&
      list[index].password === password
    ) {
      return {
        type: actionTypes.CHANGE_LOGIN,
        userName: fromJS(userName),
        login: fromJS(true),
        alert: fromJS(false),
      };
    }
  }
  return {
    type: actionTypes.CHANGE_LOGIN,
    userName: fromJS(""),
    login: fromJS(false),
    alert: fromJS(true),
  };
};

export const logoutAction = () => ({
  type: actionTypes.LOGOUT,
  login: fromJS(false),
});
