import { fromJS } from "immutable";
import * as actionTypes from "./actionTypes";

export const initialTopicListAction = (dispatch) => {
  fetch("https://ypx0352.github.io/docs/data/home.json")
    .then((response) => response.json())
    .then((data) => {
      dispatch({
        type: actionTypes.INITIAL_TOPIC_LIST,
        value: fromJS(data.topicList),
      });
    })
    .catch((err) => {
      console.log(err);
    });
};

