import { actionTypes } from ".";
import { fromJS } from "immutable";

const defaultState = fromJS({
  focused: false,
  mouseEnter: false,
  searchTrendList: [],
  page: 1,
  totalPage: 1,
  change_focused: false,
});

const returnNewStateToStore = (state = defaultState, action) => {
  switch (action.type) {
    case actionTypes.CHANGE_SEARCH_FOCUS_STATE:
      return state.set("focused", action.value);
    case actionTypes.INITIAL_SEARCH_TREND_LIST:
      return state.merge({
        searchTrendList: action.list,
        totalPage: action.totalPage,
      });
    // return state
    //   .set("searchTrendList", action.list)
    //   .set("totalPage", action.totalPage);
    case actionTypes.CHANGE_PAGE:
      if (state.get("page") === state.get("totalPage")) {
        return state.set("page", 1);
      }
      return state.set("page", state.get("page") + 1);
    case actionTypes.CHANGE_MOUSE_ENTER:
      return state.set("mouseEnter", action.value);
    case actionTypes.CHANGE_CHANGE_FOCUS:
      return state.set("change_focused", state.get('change_focused')? fromJS(false):fromJS(true));
    default:
      return state;
  }
};

export default returnNewStateToStore;
