import { fromJS } from "immutable";
import * as actionTypes from "./actionTypes";

const defaultState = fromJS({
  topicList: [],
  recommendPicLisst: [],
  qrCodeUrl: "",
  qrCodeMouseEnter: false,
  articleList: [],
  loadArticleItem: 2,
});

const returnNewStateToStore = (state = defaultState, action) => {
  switch (action.type) {
    case actionTypes.INITIAL_TOPIC_LIST:
      return state.set("topicList", action.value);
    case actionTypes.INITIAL_RECOMMEND_LIST:
      return state.set("recommendPicLisst", action.value);
    case actionTypes.INITIAL_QRCODE_URL:
      return state.set("qrCodeUrl", action.value);
    case actionTypes.CHANGE_QRCODE_MOUSE_ENTER:
      return state.set("qrCodeMouseEnter", action.value);
    case actionTypes.INITIAL_ARTICLE_LIST:
      return state.set("articleList", action.value);
    case actionTypes.LOAD_MORE:
      return state.set("loadArticleItem", action.value);

    default:
      return state;
  }
};

export default returnNewStateToStore;
