import { fromJS } from "immutable";
import { actionTypes } from ".";

export const initializeArticleListAction = (dispatch) => {
  fetch("https://ypx0352.github.io/docs/data/detail.json")
    .then((response) => response.json())
    .then((data) => {
      dispatch({
        type: actionTypes.INITIAL_ARTICLE_DETAIL_LIST,
        value: fromJS(data.articleList),
      });
    });
};
