import { fromJS } from "immutable";
import { actionTypes } from ".";
const defaultState = fromJS({
  login: false,
  userName: "",
  accountList: [],
  alert: false,
});

const returnNewStateToStore = (state = defaultState, action) => {
  switch (action.type) {
    case actionTypes.INITIAL_ACCOUNT_LIST:
      return state.set("accountList", action.value);
    case actionTypes.CHANGE_LOGIN:
      return state.merge({
        login: action.login,
        userName: action.userName,
        alert: action.alert,
      });
    case actionTypes.LOGOUT:
      return state.merge({
        login: action.login,
        userName: fromJS(""),
        alert: fromJS(false),
        accountList: fromJS([]),
      });
    default:
      return state;
  }
};

export default returnNewStateToStore;
