import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { actionCreators } from "./store";
import {actionCreators as loginActionCreators} from '../../pages/login/store'
import {
  HeaderWrapper,
  Logo,
  Nav,
  NavItem,
  NavSearch,
  Addition,
  AdditionItem,
  Searchwrapper,
  SearchInfo,
  SearchInfoTitle,
  SearchInfoTitleChange,
  SearchInfoItem,
} from "./style";

const Header = (props) => {
  const {
    focused,
    handleSearchFocuse,
    searchTrendList,
    page,
    totalPage,
    handleChangeClick,
    handleMouseEnter,
    mouseEnter,
    change_focused,
    handleChangeFocus,
    loginStatus,
    userName,
    logout,
  } = props;

  const generateSearchInfoItem = () => {
    var searchTrendListSlice;

    if (page === totalPage) {
      searchTrendListSlice = searchTrendList.slice(12 * (page - 1));
    } else {
      searchTrendListSlice = searchTrendList.slice(12 * (page - 1), 12 * page);
    }

    return searchTrendListSlice.map((item, index) => {
      return <SearchInfoItem key={index}>{item}</SearchInfoItem>;
    });
  };

  return (
    <HeaderWrapper>
      <Link to="/">
        <Logo />
      </Link>
      <Nav>
        <NavItem className="left active">Home</NavItem>
        <NavItem className="left">App</NavItem>

        {loginStatus ? (
          <NavItem className="right" onClick={logout}>
            Logout
          </NavItem>
        ) : (
          <Link to="/login">
            <NavItem className="right">Login</NavItem>
          </Link>
        )}

        <NavItem className="right">
          <span className="iconfont icon-Aa "></span>
        </NavItem>
        <Searchwrapper>
          <NavSearch
            className={focused ? "focused" : ""}
            onFocus={() => handleSearchFocuse(true, searchTrendList)}
            onBlur={() => handleSearchFocuse(false, searchTrendList)}
          />
          <span
            className={
              focused
                ? "focused iconfont icon-fangdajing"
                : "iconfont icon-fangdajing"
            }
          ></span>
          <SearchInfo
            className={focused || mouseEnter ? "" : "hide"}
            onMouseEnter={() => handleMouseEnter(true)}
            onMouseLeave={() => handleMouseEnter(false)}
          >
            <SearchInfoTitle>
              Top Search
              <SearchInfoTitleChange onClick={handleChangeClick}>
                <span
                  className={
                    change_focused
                      ? "clicked iconfont icon-spin "
                      : "iconfont icon-spin"
                  }
                ></span>
                <span className="text" onClick={handleChangeFocus}>
                  Change
                </span>
              </SearchInfoTitleChange>
            </SearchInfoTitle>
            {generateSearchInfoItem()}
            {/* {searchTrendList.map((item)=>{
              return <SearchInfoItem key={item}>{item}</SearchInfoItem>
            })} */}
          </SearchInfo>
        </Searchwrapper>
      </Nav>
      <Addition>
        <AdditionItem className="compose">
          <span className="iconfont ">&#xe6e5;</span>
          <span className="text">Compose</span>
        </AdditionItem>
        {loginStatus ? (
          <AdditionItem className="register">{userName}</AdditionItem>
        ) : (
          <AdditionItem className="register">Register</AdditionItem>
        )}
      </Addition>
    </HeaderWrapper>
  );
};

const mapStateToProps = (state) => {
  return {
    focused: state.getIn(["header", "focused"]),
    searchTrendList: state.getIn(["header", "searchTrendList"]),
    page: state.getIn(["header", "page"]),
    totalPage: state.getIn(["header", "totalPage"]),
    mouseEnter: state.getIn(["header", "mouseEnter"]),
    change_focused: state.getIn(["header", "change_focused"]),
    loginStatus: state.getIn(["login", "login"]),
    userName: state.getIn(["login", "userName"]),
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    handleSearchFocuse(value, list) {
      dispatch(actionCreators.changeSearchFocusAction(value));
      if (value && list.size === 0) {
        dispatch(actionCreators.getSearchTrendList());
      }
    },
    handleChangeClick() {
      dispatch(actionCreators.changeSearchTrendPageAction());
    },
    handleMouseEnter(value) {
      dispatch(actionCreators.changeMouseEnterAction(value));
    },
    handleChangeFocus() {
      dispatch(actionCreators.changeChangeFocusAction());
    },
    logout(){
      dispatch(loginActionCreators.logoutAction())
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Header);
